import React from 'react'
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import UseAppContext from '../../context/useAppContext'
import AddUser from './pages/AddUser'
import AdminDashoard from './pages/AdminDashoard'
import UserView from './pages/dashboard/view-user/UserView'

export default function Ads() {
  const { user } = React.useContext(UseAppContext)
  const { user_key } = user || {}

  return (
    <>
      <Router>
        <Routes>
          <Route path={`/`} element={<AdminDashoard />} />
          <Route path={`${user_key}/adduser`} element={<AddUser />} />
          <Route path={`${user_key}/view-user`} element={<UserView />} />
          {/* <Route path={`${url}/active-view`} element={<GeneratTable />} />
          <Route path={`${url}/view-meter`} element={<MeterInformation />} />
          <Route path={`${url}/applicant`} element={<ApplicantionDiscos />} />
          <Route path={`${url}/applicant-info`} element={<Applicant />} />
          <Route
            path={`${url}/view-summary`}
            element={<ShowAllocationActivities />}
          />
          <Route path={`${url}/exception`} element={<ExceptionReport />} />
          <Route path={`${url}/exempt`} element={<ExemptReport />} />
          <Route path={`${url}/transit`} element={<TransitReport />} />
          <Route path={`${url}/report`} element={<CBGenerateReport />} />
          <Route path={`${url}/disco`} element={<DiscoDashboard />} />
          <Route path={`${url}/map`} element={<MapDashboard />} />
          <Route path={`${url}/google`} element={<GoogleMapLoader />} /> */}
        </Routes>
      </Router>
    </>
  )
}
