import Menu from '@mui/material/Menu'
import MenuItem from '@mui/material/MenuItem'
import React from 'react'
import connect from '../../../context/connect_to_server'
import UseAppContext from '../../../context/useAppContext'

function RenderMenu({ isMenuOpen, handleMenuClose, anchorEl, menuId }) {
  const { adjustMany } = React.useContext(UseAppContext)

  const logout = async () => {
    handleMenuClose()
    adjustMany({ user: null, isAuthenticated: false, token: null })
    connect.clearAll()
    window.location.href = '/'
  }
  return (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      id={menuId}
      keepMounted
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>Profile</MenuItem>
      <MenuItem onClick={handleMenuClose}>My account</MenuItem>
      <MenuItem onClick={logout}>Logout</MenuItem>
    </Menu>
  )
}

export default RenderMenu
