import Reset from '@mui/icons-material/Restore'
import Block from '@mui/icons-material/Block'
import Suspend from '@mui/icons-material/PersonAddDisabled'
import { Avatar, Button, Typography } from '@mui/material'

import Box from '@mui/material/Box'

function HeaderView({ data }) {
  const { fullName } = data || {}

  const ab = fullName ? fullName.substr(0, 3) : ''

  return (
    <Box
      style={{
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
      }}
      marginY={2}
    >
      <Avatar
        sx={{
          width: 90,
          height: 90,
          marginRight: 5,
          fontSize: 40,
          textTransform: 'uppercase',
        }}
      >
        {ab}
      </Avatar>
      <Box>
        <Typography component={'h6'} variant={'h4'}>
          {fullName}
        </Typography>
        <Box
          style={{
            display: 'flex',
            justifyContent: 'flex-start',
            alignItems: 'center',
          }}
        >
          <Button startIcon={<Reset />}>Reset Password</Button>
          <Button startIcon={<Block />} sx={{ marginLeft: 1 }}>
            Block Sign-in
          </Button>
          <Button startIcon={<Suspend />} sx={{ marginLeft: 1 }}>
            Suspend User
          </Button>
        </Box>
      </Box>
    </Box>
  )
}

export default HeaderView
