import { Button, Divider, Typography } from '@mui/material'
import Box from '@mui/material/Box'
import React from 'react'
import OpenBox from '../../../../../component/alerts_boxes/OpenBox'
import SelectAutoWidth from '../../../../../component/input/SelectComponent'
import TextInput from '../../../../../component/input/TextInput'

function BodyView({ data }) {
  const {
    fullName,
    email,
    email_verified,
    role,
    abbrv,
    telephone,
    right_to_share_profile,
    last_login_info,
  } = data || {}
  const [tel, setTel] = React.useState(telephone || '')
  const [profile, setProfile] = React.useState(right_to_share_profile || '')
  const [open, setOpen] = React.useState(false)
  const [share, setShare] = React.useState(false)

  return (
    <Box marginY={2}>
      <Typography component={'h6'} variant={'h5'} sx={{ marginBottom: 2.5 }}>
        Contact Infomation
      </Typography>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX
          title={'Email Address'}
          semi={email}
          Press={() => alert('Connected')}
          // link={'Modify user name'}
        />
        <BX
          title={'Email Verified'}
          semi={email_verified ? 'Verified' : 'Not verified'}
        />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'User Full Name'} semi={fullName} />
        <BX
          title={'Telephone'}
          semi={telephone || 'Not available'}
          Press={() => setOpen(true)}
          link={'Modify telephone'}
        />
      </Box>

      <Divider />

      <Typography
        component={'h6'}
        variant={'h5'}
        sx={{ marginBottom: 2.5, marginTop: 1 }}
      >
        Profile Infomation
      </Typography>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'Role'} semi={role || 'Not available'} />
        <BX title={'User Acroynms'} semi={abbrv || 'Not avaliable'} />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX
          title={'Right to Share Profile'}
          semi={right_to_share_profile ? 'TRUE' : 'FALSE'}
          Press={() => setShare(true)}
          link={'Modify'}
        />
        <BX title={'Last login'} semi={last_login_info || 'Not avaliable'} />
      </Box>

      {open && (
        <OpenBox
          show={open}
          title={'User Modification Module'}
          cancel={() => setOpen(false)}
          onPress={() => alert('Am connected')}
          press_title={'Update'}
        >
          <p style={{ margin: '0.5rem' }}>Modify user contact number </p>
          <TextInput
            onChange={(name, value) => setTel(value)}
            name={'tel'}
            value={tel}
            label={'Modify User Contact'}
          />
        </OpenBox>
      )}

      {share && (
        <OpenBox
          show={share}
          title={'Allow User to Share Profile'}
          cancel={() => setShare(false)}
          onPress={() => alert('Am connected')}
          press_title={'Update'}
        >
          <Divider />
          <p style={{ margin: '0.5rem' }}>
            Allow user to share profile with others
          </p>
          <SelectAutoWidth
            handleInputChange={(name, value) => setProfile(value)}
            name={'profile'}
            value={profile}
            label={'Share Profile'}
            items={['Allow', 'Disallow']}
            size={'100%'}
          />
        </OpenBox>
      )}
    </Box>
  )
}

function BX({ title, semi, link, Press }) {
  return (
    <Box>
      {/* <Typography component={'h6'} variant={'body1'} sx={{ paddingX: 1 }}>
        {title}
      </Typography> */}
      <h5 style={{ padding: '0 0.5rem' }}>{title}</h5>

      <Typography component={'p'} variant={'body2'} sx={{ paddingX: 1 }}>
        {semi}
      </Typography>

      {link && (
        <Button onClick={Press} sx={{ textTransform: 'initial' }}>
          {link}
        </Button>
      )}
    </Box>
  )
}

export default BodyView
